body{
  background: #000;
  font-family: 'Source Code Pro', monospace;
  color: #fff;
  font-size: 18px;
}

.btn{
  font-size: 16px;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 50px;
  color: #fff;
  background: transparent;
  border-width: 2px;
  border-style: solid;
  border-color: #86b42b;
  display: inline-block;
  padding: 12px 20px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  text-align: center;
  font-weight: bold;
}
.btn:before, .btn:after {
  content: '';
  display: block;
  position: absolute;
  border-color: #86b42b;
  box-sizing: border-box;
  border-style: solid;
  width: 1em;
  height: 1em;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.btn:before {
  top: -6px;
  left: -6px;
  border-width: 2px 0 0 2px;
  z-index: 5;
}
.btn:after{
  bottom: -6px;
  right: -6px;
  border-width: 0 2px 2px 0;
}
.btn:hover{
  color: #353535;
  background-color: #fff;
  border-color: #fff;
}
.btn:hover:before, .btn:hover:after {
  width: calc(100% + 12px);
  height: calc(100% + 12px);
  border-color: #fff;
}

.innerpage{
  padding: 50px;
}

.heading{
  font-size: 40px;
  font-weight: bold;
  text-align: center;
}

.heading .txt{
